import {
  ActionsUnion,
  ActionsOfType as TActionsOfType,
  createAction,
} from '@martin_hotell/rex-tils';
import {resultify} from '@kevinastone/result';

import {navigate} from 'gatsby';
import idx from 'idx.macro';

import {SearchResult} from './api';

export enum ActionType {
  SET_QUERY = 'SET_QUERY',
  SET_RESULTS = 'SET_RESULTS',
  NEXT_SELECTION = 'NEXT_SELECTION',
  PREVIOUS_SELECTION = 'PREVIOUS_SELECTION',
  RESET = 'RESET',
}

export const Actions = {
  setQuery: (query: string) => createAction(ActionType.SET_QUERY, query),
  setResults: (results: SearchResult[]) =>
    createAction(ActionType.SET_RESULTS, results),
  nextSelection: () => createAction(ActionType.NEXT_SELECTION),
  previousSelection: () => createAction(ActionType.PREVIOUS_SELECTION),
  reset: () => createAction(ActionType.RESET),
};
export type Actions = ActionsUnion<typeof Actions>;

export function activateSelection(
  selection: {current: number},
  results: SearchResult[],
): void {
  const url = idx(results, _ => _[selection.current].url);
  if (url) {
    navigate(url);
  }
}

export async function setQuery(
  dispatch: (action: Actions) => void,
  api: (query: string) => Promise<SearchResult[]>,
  query: string,
): Promise<void> {
  dispatch(Actions.setQuery(query));

  (await resultify(api(query)))
    .ok()
    .map(results => dispatch(Actions.setResults(results)));
}

// Need to do this explicit aliasing because babel struggles with re-exporting
// https://github.com/babel/babel-loader/issues/603
export type ActionsOfType<Ta, Tb extends string> = TActionsOfType<Ta, Tb>;
