import {Actions, ActionType, ActionsOfType} from './actions';

type BaseReducer<TState, TActionType extends ActionType = ActionType> = (
  state: TState,
  action: ActionsOfType<Actions, TActionType>,
) => TState;

type Reducer<TState> = (
  state: TState | undefined,
  action: Actions | undefined,
) => TState;

type Reducers<State> = {
  [T in keyof State]: Reducer<State[T]>;
};

function handleActions<TState>(
  reducers: {
    [T in ActionType]?: BaseReducer<TState, T>;
  },
  intitialState: TState,
): Reducer<TState> {
  return (
    state: TState = intitialState,
    action: Actions | undefined,
  ): TState => {
    const reducer = action && reducers[action.type];
    if (action !== undefined && reducer !== null && reducer !== undefined) {
      return reducer(state, action as any);
    }

    return state;
  };
}

type BaseState = {[index: string]: any};

function combineReducers<TState extends BaseState>(
  reducers: Reducers<TState>,
): Reducer<TState> {
  return (state: TState | undefined, action: Actions | undefined): TState => {
    const incomingState = (state || {}) as TState;

    const nextState = Object.entries(reducers).reduce(
      (nextState, [key, reducer]) => ({
        [key]: reducer(incomingState[key], action),
        ...nextState,
      }),
      {},
    ) as BaseState;

    const hasChanged = Object.keys(reducers).some(
      key => incomingState[key] !== nextState[key],
    );

    return (hasChanged ? nextState : incomingState) as TState;
  };
}

const queryReducer = handleActions(
  {
    [ActionType.SET_QUERY]: (
      _: any,
      action: ActionsOfType<Actions, ActionType.SET_QUERY>,
    ) => action.payload,
  },
  '',
);

const resultsReducer = handleActions(
  {
    [ActionType.SET_RESULTS]: (
      _: any,
      action: ActionsOfType<Actions, ActionType.SET_RESULTS>,
    ) => action.payload,
  },
  [],
);

interface SelectionState {
  readonly current: number;
  readonly max: number;
}

const selectionReducer = handleActions(
  {
    [ActionType.NEXT_SELECTION]: (state: SelectionState): SelectionState => ({
      ...state,
      current: Math.min(state.current + 1, state.max),
    }),
    [ActionType.PREVIOUS_SELECTION]: (
      state: SelectionState,
    ): SelectionState => ({
      ...state,
      current: Math.max(state.current - 1, 0),
    }),
    [ActionType.SET_RESULTS]: (
      state: SelectionState,
      action: ActionsOfType<Actions, ActionType.SET_RESULTS>,
    ): SelectionState => {
      const max = action ? Math.max(action.payload.length - 1, 0) : 0;

      return {
        ...state,
        current: Math.min(state.current, max),
        max,
      };
    },
  },
  {
    current: 0,
    max: 0,
  },
);

interface SearchState {
  query: string;
  results: any[];
  selection: SelectionState;
}

const searchReducer = combineReducers<SearchState>({
  query: queryReducer,
  results: resultsReducer,
  selection: selectionReducer,
});

function resetReducer(
  state: SearchState | undefined,
  action: Actions | undefined,
): SearchState | undefined {
  return action && action.type === 'RESET' ? undefined : state;
}

export default function(
  state: SearchState | undefined,
  action: Actions,
): SearchState {
  const state2 = resetReducer(state, action);
  return searchReducer(state2, action);
}
